@media print {
  :root {
    --root__bg-color: #fff;
    --root__color: #000;
    --root__font-size: 10px;
    --root__color-scheme: light;
    --link__color: var(--root__color);
    --link-visited__color: var(--root__color);
    --link__text-underline-offset:auto;
    --link__text-decoration-line: underline;
    --link__text-decoration-thickness: auto;
    --link__text-decoration-style: solid;
    --link__text-decoration-color: var(--root__color);
    --heading-link__color: var(--root__color);
    --heading-link-visited__color: var(--root__color);
    --heading-link__text-underline-offset:auto;
    --heading-link__text-decoration-line: underline;
    --heading-link__text-decoration-thickness: auto;
    --heading-link__text-decoration-style: solid;
    --heading-link__text-decoration-color: var(--root__color);
    --input__font-size: inherit;
    --input__border-width: 1px;
    --input__border-style: solid;
    --input__border-color: grey;
    --input__color: var(--root__color);
    --input__bg-color: var(--root__bg-color);
    --input-disabled__bg-color: var(--root__bg-color);
  }
}
